import React,{useState,useEffect}  from 'react';
import { NavLink } from 'react-router-dom';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import HomeIcon from '@material-ui/icons/Home';
import MoneyOffIcon from '@material-ui/icons/MoneyOff';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import axios from 'axios';
import {parseJwt,logOutFunc} from './utils';
import swal from 'sweetalert';
import { useHistory } from "react-router-dom";

const ProfileLeft = ({active}) => {

    const [name,setName] = useState('')
    const history = useHistory();

    useEffect(() => {
    
    const fetchName = async () => {
        const parsed = parseJwt(localStorage.getItem("refresh"));
        
        const config = {
            params: {
              'user_id' : parsed.user_id
            },
          };

        await axios.get(`${process.env.REACT_APP_URL}/getname`, config)
        .then (res => {
            setName(res.data[0])
        })
        .catch(err => {
            console.log(err)
        })
    }
    fetchName()
    },[])

    const logout = () => {
        swal({
          title: "Are you sure?",
          text: "Are you sure that you want to log out?",
          icon: "warning",
          dangerMode: true,
        })
        .then(willLogOut => {
          if (willLogOut) {
            swal("Logged Out!", "You have been logged out!", "success")
            .then(okay => {
              logOutFunc()
              history.push('/');
            })
          }
        })
    }

    return (
        <> 
            <div className="profile_left">
                <div className="left_top">
                    <h2> Welcome {name.charAt(0).toUpperCase() + name.slice(1)} </h2>
                    <span className = "form_button">
                        <button className = "simple_button" onClick = {logout}> Logout <ExitToAppIcon /> </button>
                    </span>
                </div>

                <div className="profile_navs">

                    {
                        active === "first"?
                        <>
                            <NavLink exact to = "/profile" >
                                <p className = "nav_active"> <HomeIcon /> Home </p> 
                            </NavLink> 

                            <NavLink exact to = "/expenses" >
                                <p> <MoneyOffIcon/> Expenses </p>
                            </NavLink> 

                            <NavLink exact to = "/income" >
                                <p> <AttachMoneyIcon/> Incomes </p>
                            </NavLink> 

                            <NavLink exact to = "/todolist" >
                                <p> <AttachMoneyIcon/> My Wish-List </p>
                            </NavLink> 

                        </>
                        :
                        (
                            active === "second"?
                            <>
                                <NavLink exact to = "/profile" >
                                    <p> <HomeIcon/> Home </p>
                                </NavLink> 

                                <NavLink exact to = "/expenses" >
                                    <p className = "nav_active"> <MoneyOffIcon /> Expenses  </p>
                                </NavLink> 

                                <NavLink exact to = "/income" >
                                    <p> <AttachMoneyIcon /> Incomes </p>
                                </NavLink> 

                                <NavLink exact to = "/todolist" >
                                    <p> <AttachMoneyIcon/> My Wish-List </p>
                                </NavLink> 
                            </>
                            :
                            active === "third"?
                            <>
                                <NavLink exact to = "/profile" >
                                    <p> <HomeIcon /> Home </p>
                                </NavLink> 

                                <NavLink exact to = "/expenses" >
                                    <p> <MoneyOffIcon /> Expenses </p>
                                </NavLink> 

                                <NavLink exact to = "/income" >
                                    <p className = "nav_active"> <AttachMoneyIcon/> Incomes </p>
                                </NavLink> 

                                <NavLink exact to = "/todolist" >
                                    <p> <AttachMoneyIcon/> My Wish-List </p>
                                </NavLink> 
                            </>
                            :
                            <>
                                <NavLink exact to = "/profile" >
                                    <p> <HomeIcon /> Home </p>
                                </NavLink> 

                                <NavLink exact to = "/expenses" >
                                    <p> <MoneyOffIcon /> Expenses </p>
                                </NavLink> 

                                <NavLink exact to = "/income" >
                                    <p > <AttachMoneyIcon/> Incomes </p>
                                </NavLink> 

                                <NavLink exact to = "/todolist" >
                                    <p className = "nav_active"> <AttachMoneyIcon/> My Wish-List </p>
                                </NavLink> 
                            </>
                        )
                    }
                </div>
            </div> 
        </>
    )
}

export default ProfileLeft;